import { Component, OnInit, ViewChild, ElementRef, ChangeDetectionStrategy } from "@angular/core";

import { BehaviorSubject } from "rxjs/BehaviorSubject";

import { Role } from "../shared/role/role";
import { PermissionService } from "../shared/role/permission.service";
import { AuthService } from "./../shared/user/auth.service";
import { RoleService } from "../shared/role/role.service";
import { UserService } from "../shared/user/user.service";

@Component({
  selector: "app-role",
  templateUrl: "./role.component.html",
  styleUrls: ["./role.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RoleComponent implements OnInit {
  @ViewChild("roleDetailsModal") roleDetailsModal;
  @ViewChild("createRoleModal") createRoleModal;
  @ViewChild("deleteRoleModal") deleteRoleModal;
  @ViewChild("createRole") createRole;
  @ViewChild("body") body;

  public role: Role;
  public roles;
  public roleCount: number;
  public permissions: { value: string; label: string }[] = [];
  public roleDetails;
  public maxUserCount = 90;
  public hidden = false;

  public fetchDone: boolean;
  public roleForDelete;
  public roleDetailsModalHeaderColor = "";
  public headerColors = ["#f44336", "#009ef6", "#f0c315", "#3fa742", "#d0021b", "#005494"];
  public searchQuery: string;

  public PermissionActive: boolean;

  constructor(
    public _roleService: RoleService,
    private _permissionService: PermissionService,
    private _userService: UserService,
    private _authService: AuthService
  ) {
    this.roleDetails = new Role();
    this.roleForDelete = new Role();
    this.roleCount = 0;

    this.searchQuery = "";
    this.fetchDone = false;
    this.PermissionActive = false;
  }

  ngOnInit() {
    this._roleService.getRoles().subscribe();

    this.roles = this._roleService.roles;
    this.roles.subscribe(data => {
      this.roleCount = data.length;
    });

    this.permissions = this._permissionService.getPermissions().sort((a, b) => {
      if (a.label < b.label) {
        return -1;
      } else if (a.label > b.label) {
        return 1;
      } else {
        return 0;
      }
    });

    this.role = new Role();
  }

  showRoleDetail(role, index) {
    this.roleDetails = Object.assign(new Role(), role);
    this.roleDetailsModalHeaderColor = this.headerColors[index % 6];
    this.roleDetailsModal.show();
  }

  submit() {
    const userId = this._authService.getUserId();

    for (const i in this.role) {
      if (this.role.userId !== userId) {
        this.role.userId = userId;
      }
    }
    this._roleService.createOrEditRole(Object.assign(new Role(), this.role));
    this.createRoleModal.hide();
  }

  promptDeleteRole(role: Role) {
    this.roleForDelete = role;
    this.deleteRoleModal.show();
  }

  deleteRole(role: Role) {
    this.roleDetailsModal.hide();
    this.deleteRoleModal.hide();
    this._roleService.deleteRole(role);
  }

  searchValueChange(q) {
    this.searchQuery = q;
    this._roleService.searchTerm.next(this.searchQuery);
  }

  getUserNameByUserId(userId) {
    return this._userService.getUserNameByUserId(userId);
  }

  openCreateModal() {
    this.role = new Role();
    this.createRole.reset();
    this.createRoleModal.show();
  }

  editRole(role: Role) {
    this.role = role;
    this.createRoleModal.show();
  }
}



